import React, { useState, useEffect } from 'react';
// import MediaQuery from 'react-responsive'

import {ReactWebGLInteractiveImage} from '@WilsonPhooYK/react-webgl-interactive-image';
import Logo from './logo.png';
// import LogoSmall from './logo_small.png';
import Logo2 from './logo2.png';
// import Logo2Small from './logo2_small.png';

const App = () => {
  const [image, setImage] = useState(Logo);

  useEffect(() => {
    setTimeout(() => {
      document.getElementById('react-canvas').children[0].removeChild(document.getElementById('react-canvas').children[0].children[1])
    }, 10);
  }, []);

  return (
    <>
      <div id="id-webglcanvas">
        <div style= {{
          position: 'absolute',
          top: '15px',
          left: '5px',
          zIndex: 100,
          display: 'flex',
          alignItems: 'center',
        }}>
          <button
              style={{
                height: '40px',
                width: '80px',
                borderRadius: '5px'
              }}
              onClick={() => setImage(image === Logo ? Logo2 : Logo)}
            >
              Change
            </button>
            <div style={{ fontFamily: 'arial', marginLeft: '10px' }}>Hover mouse/finger on image</div>
        </div>
        <div id="react-canvas" style={{
          height: '100%',
          width: '100%',
        }}>
          <ReactWebGLInteractiveImage
            canvasWidth={600}
            canvasHeight={600}
            image={image}
            useResponsive
            responsiveRefWidth={1200}
            responsiveRefWidthFactor={0.5}
            pixelSize={4}
            pixelBufferPadding={0.5}
            colliderRadius={100}
            colliderStrength={300}
            isAntialias
          />
        </div>
      </div>
    </>
  );
}

export default App;
